.card_item {
  // Card one
  &.one {
    margin-bottom: 30px;
    max-width: 280px;
    @include sm {
      margin-left: auto;
      margin-right: auto;
    }
    .card_head {
      transition: all 0.35s cubic-bezier(0.4, 0, 0.2, 1);
      transform: scale(1);
      position: relative;
      margin-top: 38px;
      perspective: 160px;
      perspective-origin: 50% -100%;
      @include md {
        margin-top: 46px;
      }
      img {
        width: 100%;
        border-radius: 10px;
      }
      &:hover {
        transform: scale(1.02);
        .card_img::after {
          transform: translate3D(0, 0, -20px);
        }
        .card_img::before {
          transform: translate3D(0, 0, -10px);
        }
      }

      .card_img {
        width: 100%;
        background-color: transparent;
        transform-style: preserve-3d;
        position: relative;
        display: grid;
        place-items: center;
        &::before {
          background: linear-gradient(
            180deg,
            #161f50 0%,
            rgba(16, 23, 62, 0) 100%
          );
          z-index: -1;
          transform: translate3D(0, 0, -20px);
        }
        &::after {
          background: linear-gradient(
            180deg,
            #10173e 0%,
            rgba(16, 23, 62, 0) 100%
          );
          z-index: -2;
          transform: translate3D(0, 0, -40px);
        }
      }
      .card_img,
      .card_img::before,
      .card_img::after {
        border-radius: 8px;
      }
      .card_img::before,
      .card_img::after {
        position: absolute;
        width: 100%;
        height: 100%;
        content: "";
        display: block;
        transition: transform 200ms;
      }
      .icon {
        position: absolute;
        top: 10px;
        right: 10px;
        width: 25px;
        height: 25px;
        background-color: rgba(255, 255, 255, 0.18);
        border-radius: 7px;
        display: flex;
        justify-content: center;
        align-items: center;
        i {
          color: white;
          font-size: 13px;
        }
      }
    }
    .card_body {
      .card_title {
        font-size: 16px;
      }
      .bid_details {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 10px 16px;
        background-image: url(../img/bg/gardient_bg.png);
        background-size: cover;
        background-repeat: no-repeat;
        border-radius: 10px;
        .status {
          display: flex;
          align-items: center;
        }
      }
    }
  }
  // Card two
  &.two {
    display: flex;
    align-items: center;
    .card_body {
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
  }
  // Card three
  &.three {
    display: flex;
    align-items: center;
    background-image: url(../img/bg/gardient_bg.png);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: top;
    padding: 20px;
    border: 1px solid $color_border;
    border-radius: 10px;
    .card_body {
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      .icon {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 30px;
        width: 30px;
        border-radius: 10px;
        background-color: $color_brand;
        transition: all 0.35s cubic-bezier(0.4, 0, 0.2, 1);
        &:hover {
          height: 31.5px;
          width: 31.5px;
        }
        i {
          font-size: 18px;
          font-weight: bold;
          color: white;
        }
      }
      .rank {
        color: $color_text;
        display: flex;
        justify-content: flex-end;
      }
    }
  }
  // Card four
  &.four {
    background-image: url(../img/bg/gardient_bg.png);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: top;
    padding: 20px;
    border: 1px solid $color_border;
    border-radius: 10px;
    margin-bottom: 20px;

    .icon {
      font-weight: bold;
      font-size: 19px;
    }
    .items_number,
    .sales_number {
      font-size: 18px;
      font-weight: bold;
    }
    .statistics {
      display: flex;
      align-items: center;

      padding: 3.5px 8px;
      border-radius: 50px;
      font-size: 14px;
      i {
        font-weight: bold;
      }
      &.up {
        background-color: $color_icon_green;
        color: $color_blue;
      }
      &.down {
        background-color: $color_icon_red;
        color: $color_pink;
      }
    }
    .arrow_icon {
      width: 24px;
      height: 24px;
      display: flex;
      align-items: center;
      justify-content: center;
      margin-right: 5px;
      background-color: $color_light;
      font-size: 14px;
      font-weight: bold;
      border-radius: 50%;
    }
    .hr_v {
      height: 120px;
    }
  }
  // Card five
  &.five {
    background-color: $color_secondary;
    padding: 15px;
    border: 1px solid $color_border;
    border-radius: 10px;
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    .items_number {
      font-size: 18px;
      font-weight: bold;
    }
    .icon {
      background-image: url(../img/bg/gardient_bg.png);
      background-size: cover;
      background-repeat: no-repeat;
      background-position: top;
      padding: 20px;
      border: 1px solid $color_border;
      border-radius: 10px;
      width: 60px;
      height: 60px;
      display: flex;
      justify-content: center;
      align-items: center;
      i {
        color: white;
        font-size: 28px;
        font-weight: normal;
      }
    }
  }
}
