.marketplace {
  overflow: hidden;
  display: flex;
  margin-bottom: 20px;
  @include max-lg {
    max-width: max-content;
    flex-direction: column;
    margin-left: auto;
    margin-right: auto;
  }
  .hero_marketplace {
    overflow: hidden;
    background-image: url(../img/bg/cover.png);
    background-repeat: no-repeat;
    background-size: cover;
    @include overlay {
      border-radius: $border_radius;
    }
    .hero_title {
      font-size: 50px;
      width: 100%;
      max-width: 30rem;
      @include max-lg {
        max-width: max-content;
      }

      @include sm {
        font-size: 40px;
      }
    }
  }
  .categories {
    .items {
      display: flex;
      flex-wrap: wrap;
      margin-bottom: -20px;
      .item {
        margin-right: 20px;
        margin-bottom: 20px;
        background: $color_secondary;
        color: $color_white;
        border-radius: 50px;
        background-image: linear-gradient(#10173e, #10173e),
          linear-gradient(
            180deg,
            rgba(255, 255, 255, 0.19) 0%,
            rgba(255, 255, 255, 0) 100%
          );
        background-origin: border-box;
        background-clip: content-box, border-box;
        border: none;
        padding: 1px;
        &:last-of-type {
          margin-right: 0;
        }
        .wrapper {
          display: flex;
          align-items: center;
          padding: 11px 20px;
        }
      }
    }
  }
}
